import React, { useState } from "react";
import { Link } from "react-router-dom";
import style from "./form.module.css";
import { useDispatch, useSelector } from "react-redux";
import { login } from "../userredux/useractions";
const Loginscreen = ({ location, history }) => {
  const dispatch = useDispatch();
  const [email, setemail] = useState("");
  const [password, setpassword] = useState("");
  // const redirect = location.search ? location.search.split("=")[1] : "/";

  const userlogin = useSelector((state) => state.userlogin);
   
  const { error, loading, userinfo } = userlogin;
 
  const submithandeler = (e) => {
    e.preventDefault();
    dispatch(login(email, password));
  };

  return (
    <form className={style.form} onSubmit={submithandeler}>
      <div className={style.vaselink}>
        <h1 style={{ display: "inline-block" }}>Sign In</h1> new Customer?{" "}
        <Link to={"/users/register"}>Register</Link>
      </div>
      {error && <h1>{error}</h1>}
      {loading && <h1>loading...</h1>}
      <label>Username/Email Address</label>
      <input
        type="text"
        onChange={(e) => setemail(e.target.value)}
        placeholder="enter email"
        value={email}
      />
      <label>Password</label>
      <input
        type="password"
        placeholder="enter Password"
        onChange={(e) => setpassword(e.target.value)}
        value={password}  
      />
      <div className={style.buttoncontainer} style={{ marginTop: "1rem" }}>
        <button type="submit">Log In</button>
      </div>
    </form>
  );
};

export default Loginscreen;
