import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getuserdetails, updateusers } from "../userredux/useractions";
import { userupdatereset } from "../userredux/userconstants";
import style from "./form.module.css";
const Usereditscreen = ({ match, history }) => {
  const dispatch = useDispatch();
  const userid = match.params.id;
  const [username, setname] = useState("");
  const [email, setemail] = useState("");
  const [isadmin, setisadmin] = useState(false);

  const userdetails = useSelector((state) => state.userdetails);
  let { error, loading, user } = userdetails;
   
  if (user === undefined) {
    user = {};
    history.push("/users/admin/userlist");
  }
  // in vase ine ke dar har lahze che halatie
  const userupdate = useSelector((state) => state.userupdate);
   
  const {
    error: errorupdate,
    loading: loadingupdate,
    success: successupdate,
  } = userupdate;

  useEffect(() => {
    if (successupdate) {
      dispatch({ type: userupdatereset });
      history.push("/users/admin/userlist");
    } else {
      if (!user.username || user.id !== Number(userid)) {
        dispatch(getuserdetails(userid));
      } else {
        setname(user.username);
        setemail(user.email);
        setisadmin(user.isadmin);
      }
    }
  }, [user, userid, successupdate, history, dispatch]);

  const submithandeler = (e) => {
    e.preventDefault();
     
    dispatch(updateusers({ id: user.id, username, email, isadmin }));
  };

  return (
    <div>
      <>
        <h1>Edit User</h1>
        {loadingupdate && <h1>loading...</h1>}
        {errorupdate && <h1>{errorupdate}</h1>}
        {error && <h1>{error}</h1>}
        {loading && <h1>loading...</h1>}
        <form className={style.form} onSubmit={submithandeler}>
          <label>UserName:</label>
          <input
            type="text"
            onChange={(e) => setname(e.target.value)}
            required
            placeholder="enter your name"
            value={username}
          />
          <label>Email Address</label>
          <input
            type="email"
            onChange={(e) => setemail(e.target.value)}
            placeholder="enter your email"
            value={email}
          />
          <div style={{ display: "flex", alignItems: "center" }}>
            <input
              type="checkbox"
              id="checked"
              checked={isadmin}
              onChange={(e) => setisadmin(e.target.checked)}
              
            />
            <label style={{ margin: "1rem 0" }}> is admin</label>
          </div>
          <button type="submit">update</button>
        </form>
      </>
    </div>
  );
};

export default Usereditscreen;
