import React from "react";
import {  NavLink } from "react-router-dom";
import {  useSelector } from "react-redux";
import style from "./sidebar.module.css";

const Sidebar = (props) => {
  const data = useSelector((state) => state.headerdata);
 
  const classname = data&&data.headerdata[1]&&data.headerdata[1].className;
  let hclass;
  switch (classname) {
    case "users":
      hclass = style.users;
      break;
    case "leitner":
      hclass = style.leitner;

      break;

    default:
      break;
  }
  return (
    <>
      {props.kolstate && (
        <div className={style.kol} onClick={props.onClick}>
          <div
            className={`${style.sidebar} ${hclass} ${
              props.sidestate ? style.sidebarshow : style.sidebarclosed
            }`}
          >
            <nav>
              <ul>
                <li>
                  <NavLink activeClassName={style.active} to="/users/profile/">USER</NavLink>
                </li>
                <li>
                  <NavLink activeClassName={style.active}  to="/le504/">504</NavLink>
                </li>
                <li>
                  <NavLink activeClassName={style.active} to="/le1100">1100</NavLink>
                </li>
                <li>
                  <NavLink activeClassName={style.active} to="/le800">800</NavLink>
                </li>
                <li>
                  <NavLink activeClassName={style.active} to="/le3500">3500</NavLink>
                </li>
                <li>
                  <NavLink activeClassName={style.active} to="/lecollins">COLLINS</NavLink>
                </li>
                <li>
                  <NavLink activeClassName={style.active} to="/lebarrens">BARRENS</NavLink>
                </li>
              </ul>
            </nav>
          </div>
        </div>
      )}
    </>
  );
};

export default Sidebar;
