import React, { useState ,useEffect } from "react";
import { Link } from "react-router-dom";
import style from "./form.module.css";
import { useDispatch, useSelector } from "react-redux";
import { register } from "../userredux/useractions";
 

const Registerscreen = ({ location, history }) => {
  const dispatch = useDispatch();
  const [username, setname] = useState("");
  const [email, setemail] = useState("");
  const [password, setpassword] = useState("");
  const [confirmpassword, setconfirmpassword] = useState("");
  const [message, setmessage] = useState("");
  const userregister = useSelector((state) => state.userregister);
  const { error, loading  } = userregister;
  const userlogin = useSelector((state) => state.userlogin);
 
   
  const submithandeler = (e) => {
    e.preventDefault(); 
    if(password!==confirmpassword){setmessage('passwords do not match')}else{
        dispatch(register(username,email,password));   
        // history.push('/');
    }
   
  };
 
  return  (
    <form className={style.form} onSubmit={submithandeler}>
  <div className={style.vaselink}>
        <h1 style={{ display: "inline-block" }}>Sign In</h1> Have an account?{" "}
        <Link to={"/users/login"}>Login</Link>
      </div>
    {error&&<h1>{error}</h1> }
    {loading&&<h1>loading...</h1> }
      <label>UserName:</label>
      <input
        type="text"
        onChange={(e) => setname(e.target.value)}
        required
        placeholder="enter your name"
        value={username}
      />
        <label>Email Address</label>
      <input
        type="email"
        onChange={(e) => setemail(e.target.value)}
  
        placeholder="enter your email"
        value={email}
      />
      <label>Password</label>
      <input
        type="password"
        placeholder="enter Password"    
        onChange={(e) => setpassword(e.target.value)}
        value={password}
      />
      <label>Confirm Password</label>
       <input
        type="password"
        placeholder="confirm Password" 
        onChange={(e) => setconfirmpassword(e.target.value)}
        value={confirmpassword}
      />
      {<p>{message}</p>}
      <div className={style.buttoncontainer}>
   
        <button type="submit">Sign Up</button>
      </div>
    </form>

  )
 
 
};

export default Registerscreen;
