function App() {
  const userlogin = useSelector((state) => state.userlogin);
  const [sidestate, sidegir] = useState(false);
  const [kolstate, kolgir] = useState(false);

  const sidebarclickhandler = () => {
    sidegir((p) => !p);
    setTimeout(() => {
      kolgir((p) => !p);
    }, 100);
  };
  const sidbaron = () => {
    setTimeout(() => {
      sidegir((p) => !p);
    }, 400);
    setTimeout(() => {
      kolgir((p) => !p);
    }, 300);
  };
 const javab=<div className="peigham"><h2 >لطفا طرح مورد نظر را خریداری نمایید</h2></div>
  return (
    <Router>
      <Mainheader onClick={sidbaron} />
      {kolstate && (
        <Sidebar
          sidestate={sidestate}
          kolstate={kolstate}
          onClick={sidebarclickhandler}
        />
      )}
      <Suspense fallback={<div>Loading...</div>}>
        <Switch>
          
          <Route path="/le504">
            {userlogin.userinfo &&userlogin.userinfo.userplan &&userlogin.userinfo.userplan.includes('le504') ? (<Le504 />) : (javab)}
          </Route>
          <Route path="/le1100">
            {userlogin.userinfo &&userlogin.userinfo.userplan &&userlogin.userinfo.userplan.includes('le1100') ? (<Le1100 />) : (javab)}
          </Route>
          <Route path="/le800">
            {userlogin.userinfo &&userlogin.userinfo.userplan &&userlogin.userinfo.userplan.includes('le800') ? (<Le800 />) : (javab)}
          </Route>
          <Route path="/le3500">
            {userlogin.userinfo &&userlogin.userinfo.userplan &&userlogin.userinfo.userplan.includes('le3500') ? (<Le3500 />) : (javab)}
          </Route>
          <Route path="/lecollins">
            {userlogin.userinfo &&userlogin.userinfo.userplan &&userlogin.userinfo.userplan.includes('lecollins') ? (<Collins />) : (javab)}
          </Route>
          <Route path="/lebarrens">
            {userlogin.userinfo &&userlogin.userinfo.userplan &&userlogin.userinfo.userplan.includes('lebarrens') ? (<Barrens />) : (javab)}
          </Route>
          <Route component={Appuserscreen} path="/users" />
          <Route component={Home} exact path="/" />
          {/* <Route component={Page404}   path="*" /> */}
        </Switch>
      </Suspense>
    </Router>
  );
}

export default App;
