import React, {   useEffect } from "react";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { listusers, deleteusers } from "../userredux/useractions";
 const Userlistscreen = ({ history }) => {
  const dispatch = useDispatch();
  const userlist = useSelector((state) => state.userlist);
  const { loading, error, users } = userlist;
  const userlogin = useSelector((state) => state.userlogin);
  const { userinfo } = userlogin;
  const userdelete = useSelector((state) => state.userdelete);
  const { success: successdelete } = userdelete;

  const deletehandler = (id) => {
    if (window.confirm("mikhai pakesh koni?")) {
      dispatch(deleteusers(id));
    }
  };

  useEffect(() => {
    if (userinfo && userinfo.isadmin) {
      dispatch(listusers());
    } else {
      history.push("/login");
    }
  }, [dispatch, history, successdelete,userinfo]);
  return (

    <div>
      <h1>users</h1>
      {loading ? (
        <h1>loading...</h1>
      ) : error ? (
        <h1>{error}</h1>
      ) : (
        <table   >
          <thead>
            <tr>
              <th>ID</th>
              <th>NAME</th>
              <th>EMAIL</th>
              <th>ADMIN</th>
            </tr>
          </thead>
          <tbody>

          {users.map((user) => (
            <tr key={user.id}>
              <td>{user.id}</td>
              <td>{user.username}</td>
              <td>{user.email}</td>
              <td>
                {user.isadmin ? (
                  <i className="fas fa-ckeck" style={{ color: "green" }}>
                    hast
                  </i>
                ) : (
                  <i className="fas fa-ckeck" style={{ color: "red" }}>
                    nist
                  </i>
                )}
              </td>
              <td>
                <Link
                  variant="light"
                  className="btn-sm"
                  to={`/users/admin/user/${user.id}/edit`}
                >
                  <button>
                    <i className="fas fa-edit">edit</i>
                  </button>
                </Link>
                <button
                  variant="lisht"
                  className="btn-sm"
                  onClick={() => deletehandler(user.id)}
                >
                  <i className="fas fa-trash" style={{ color: "red" }}>
                    delete
                  </i>
                </button>
              </td>
            </tr>
          ))}
          </tbody>
        </table>
      )}
    </div>
  );
};

export default Userlistscreen;
