import React from "react";
import { NavLink } from "react-router-dom";
import classes from "./mainheader.module.css";
import { useDispatch, useSelector } from "react-redux";
import Menu from "../assets/menu";
import { logout } from "../users/userredux/useractions";
const Mainheader = (props) => {
  const data = useSelector((state) => state.headerdata);
  
  const classname = data && data.headerdata[1] && data.headerdata[1].className;
  const userinfo = useSelector((state) => state.userlogin);
  const dispatch = useDispatch();

  const logouthandler = () => {
    dispatch(logout());
  };
  let hclass;
  switch (classname) {
    case "users":
      hclass = classes.users;
      break;
    case "leitner":
      hclass = classes.leitner;
      break;
    default:
      break;
  }

  return (
    <header className={`${classes.header} ${hclass}`}>
      <Menu className={classes.menu} onClick={props.onClick} />
      {data.headerdata && (
        <nav>
          <ul>
            {data.headerdata[0] !== undefined &&
              data.headerdata[0].array.map((i, j) => (
                  i.name && (
                    <li className={classes.liholder} key={j}>                   
                        <NavLink
                          activeClassName={classes.active}
                          exact={true}
                          to={i.to}
                        >
                          {i.name}
                        </NavLink>    
                    </li>
                  )
               
              ))}
            {userinfo.userinfo ? (
              <li>
                <NavLink onClick={logouthandler} to="#">
                  Logout
                </NavLink>
              </li>
            ) : (<>
              <li>
                <NavLink to="/users/login">Login</NavLink>
              </li>
              <li>
              <NavLink to="/users/register">Register</NavLink>
            </li>
            </>
            )}
          </ul>
        </nav>
      )}
    </header>
  );
};

export default Mainheader;
