import React, { useEffect } from "react";
import Loginscreen from "./Loginscreen";
import Registerscreen from "./registerscreen";
import Profilescreen from "./profilescreen";
import Userlistscreen from "./userlistscreen";
import Usereditscreen from "./Usereditscreen";
import Home from "../../home";
import { useDispatch, useSelector } from "react-redux";
import {
  Switch,
  Route,
  // Redirect
} from "react-router-dom";

const Appuserscreen = () => {
  const userlogin = useSelector((state) => state.userlogin);
  const dispatch = useDispatch();
  
  const Register = userlogin.userinfo? {}: { to: "/users/register", name: "Register" };
  const Userlist =userlogin.userinfo && userlogin.userinfo.isadmin? { to: "/users/admin/userlist", name: "Userllist" }: {};
  const profile = userlogin.userinfo? { to: "/users/profile", name: "Profile" }: {};
  
  const headerarray = [{array: [{ to: "/", name: "Home" },  Userlist, profile],},
    { className: "users" },
  ];

  useEffect(() => {
    dispatch({ type: "arraygir", payload: headerarray });
  }, [ userlogin]);
  return (
    
      <Switch>
        {/* agha in home mitoone page profile user bashe va home dakhele app.js landing page. */}
        <Route component={Home} exact path="/users/home" />
        <Route component={Userlistscreen} path="/users/admin/userlist" />
        <Route component={Usereditscreen} path="/users/admin/user/:id/edit" />
        <Route component={Profilescreen} path="/users/profile" />
        {userlogin.userinfo ? <Home />:<Route component={Registerscreen} exact path="/users/register" />}
        {userlogin.userinfo ? <Home />:<Route component={Loginscreen} path="/users/login" />}
      </Switch>
    
  );
};

export default Appuserscreen;
