import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { updateuserprofile } from "../userredux/useractions";
import {userresetprofile} from "../userredux/userconstants"
import style from "./form.module.css";
const Profilescreen = ({ history }) => {
  const dispatch = useDispatch();
  const [username, setname] = useState("");
  const [email, setemail] = useState("");
  const [password, setpassword] = useState("");
  const [confirmpassword, setconfirmpassword] = useState("");
  const [message, setmessage] = useState("");
const [updatemessage,updatemessagegir]=useState("")
  const userlogin = useSelector((state) => state.userlogin);
  const { userinfo } = userlogin;
  //  dar inja etelaat az usere login shode gerefte mishavad
  const updatestate = useSelector((state) => state.userupdateprofile);
  const {success}=updatestate
 

  useEffect(() => {
    // security:
    if (!userinfo) {
      history.push("/login");
    } else {
      setname(userinfo.username);
      setemail(userinfo.email);
    }
    if(success){updatemessagegir("updated")}
 
    dispatch({type:userresetprofile})
  }, [dispatch, history, userinfo,success]);

  const submithandeler = (e) => {
    e.preventDefault();

    if (password !== confirmpassword) {
      setmessage("passwords do not match");
      return;
    } else {
      setmessage("");
    }

 setTimeout(() => {
      updatemessagegir("")
    }, 2000);

    dispatch(
      updateuserprofile({
        username: username,
        email: email,
        password: password,
      })
    );
  };
  return (
 

    <form className={style.form} onSubmit={submithandeler}>
      <h1>Update your Account</h1>
      <label>UserName:</label>
      <input
        type="text"
        onChange={(e) => setname(e.target.value)}
        required
        placeholder="enter your name"
        value={username}
      />
      <label>Email Address</label>
      <input
        type="email"
        onChange={(e) => setemail(e.target.value)}
        placeholder="enter your email"
        value={email}
      />
      <label>Password</label>
      <input
        type="password"
        placeholder="enter Password"
        onChange={(e) => setpassword(e.target.value)}
        value={password}
      />
      <label>Confirm Password</label>
      <input
        type="password"
        placeholder="confirm Password"
        onChange={(e) => setconfirmpassword(e.target.value)}
        value={confirmpassword}
      />
      {<p>{updatemessage}</p>}
      {<p>{message}</p>}
      <div className={style.buttoncontainer}>
        <button type="submit">Update</button>
      </div>
    </form>
 
  );
};

export default Profilescreen;
